.questions {
    /* min-height: 50vh; */
    display: flex;
    flex-direction: column;
    /* align-items: center;
    justify-content: center; */
    font-size: calc(10px + 2vmin);
    color: white;
    padding-top: 10px;
    padding-bottom: 50px;

    padding-left: 200px;
    padding-right: 200px;
}

a {
    color: rgb(226, 226, 226);
    text-decoration: none;
    font-style: italic;
    text-align: center;
    color: white;
}

h4 {
    text-align: center;
    color: white;
    font-weight: normal;
}

h2 {
    font-size: 45px;
    flex-wrap: wrap;
    text-align: center;
    color: white;
}

h3 {
    text-align: center;
    color: white;
}

ul {
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 25%;
}

li {
    font-size: large;
    color: white;
}

.center {
    display: 'flex';
    width: 100%;
    max-width: 60%;
    margin: 0 auto;
    justify-content: center;
}

.centerGif {
    display: 'flex';
    margin: 0 auto;
    justify-content: center;
    max-width: 75vw;
    max-height: 75vh;
}

@media (max-width: 900px) {
    .questions {
        flex-direction: column;
        padding: 25px;
    }
}